
.Main_nav
{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;
    padding: 0 2rem;
    background-color: #193350;
    color: #eee;
}
.navbar
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar ul
{
    display: flex;
}
.navbar ul li
{
    list-style: none;
}
.navbar ul li a
{
    margin: 0 2rem;
    color: #eee;
    text-decoration: none;
    font-size: 25px;
}
.navbar ul li a:hover
{
    color: #db2b39;
}
