
.main_addition
{
    width: 1170px;
    flex-wrap: wrap;
    margin: auto;
    /* background-color: #448; */
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.sub_addition
{
    background-color: aliceblue;
    border-radius: 18px;
    box-shadow: 0px 0px 12px 5px #273c75;
    flex-basis: 45%;
    margin: 10px 0px;
    text-align: center;
    padding: 22px 0px;
    transition: all 1s ease;
}
.btn
 {
   padding: 5px;
   margin-top: 30px;
   margin-bottom: 30px;
   border-radius: 10px;
   background: transparent;
   border: 2px solid #f88809;
   font-size: 20px;
   cursor: pointer;
   font-weight: bolder;
   margin:30px 5px;
   transition: 0.8s all ease-in-out 0s;
 }
 .btn:hover
 {
   background-color: #341f97;
   color: #fff;
   box-shadow: 2px 2px 2px 2px #222f3e;
 }

 .input
 {
    width: 80%;
    font-size: 20px;
    padding: 15px;
    text-align: center;
 }
 .heading
 {
  text-align: center;
  padding-top: 100px;
  color: #fff;
 }